import React from "react";
import { Fade, Slide } from "react-awesome-reveal";
import { Link } from "react-scroll";
import "../ServicesScreen/services.scss";
import ServiceBox from "../../components/ServiceBox/ServiceBox";
import teampicture from "../../assets/teampicture.svg";
import {
  faBorderAll,
  faMagnifyingGlass,
  faUserGroup,
} from "@fortawesome/free-solid-svg-icons";

function ServicesScreen() {
  return (
    <div className="services" id="services">
      <div className="services__content">
        <div className="services__text-section">
          <Slide direction="left" triggerOnce>
            <h3 className="services__heading">
              <span className="services__span">
                <strong> </strong>
              </span>
              <strong></strong>
            </h3>
          </Slide>
          <Slide direction="left" triggerOnce>
            <p className="services__paragraph">
                   ,  
                     .
            </p>
          </Slide>
        </div>
        <div className="services__box">
          <div className="service__box-item">
            <Fade direction="up" triggerOnce>
              <ServiceBox
                text={
                  "    ,        .       ."
                }
                icon={faUserGroup}
                caption={""}
                margin={"mt32"}
              />
            </Fade>
          </div>
          <div className="service__box-item">
            <Fade direction="up" triggerOnce>
              <ServiceBox
                text={
                  " 4          ,   400 000  ."
                }
                icon={faMagnifyingGlass}
                caption={""}
                margin={"mt32"}
              />
            </Fade>
          </div>
          <div className="service__box-item">
            <Fade direction="up" delay={300} triggerOnce>
              <ServiceBox
                text={
                  "   ,   ,    ."
                }
                icon={faBorderAll}
                caption={""}
                margin={"mt32"}
              />
            </Fade>
          </div>
          {/* <div className="service__box-odd">
            <Fade direction="up" delay={400} triggerOnce>
              <ServiceBox
                text={
                  "    ,        .       ."
                }
                svg={teampicture}
                caption={""}
                className={"servicebox"}
              ></ServiceBox>
            </Fade>
          </div> */}
        </div>
      </div>
    </div>
  );
}

export default ServicesScreen;
